.rk-product-list{
  margin-bottom: 60px;
  position: relative;
  &__container{
    //position: absolute;
    //left: 0;
    //top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.rk-hr-srolling{
  overflow: auto;
  //position: absolute;
  //left: 0;
  //top: 0;
}

.rk-collect-item{
  display: flex;
  flex-direction: column;
  text-decoration: none!important;
  color: #000;
  padding: 0 5px;
  margin-bottom: 15px;
  flex: 0 0 auto;
  &_scrolling{
    max-width: 280px;
  }
  &__img{
    padding: 30px;
    background: linear-gradient(180deg, #F8F8F8 0%, rgba(248, 248, 248, 0) 100%);
    margin-bottom: 20px;
    img{
      max-width: 100%;
    }
  }

  &__title{
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    width: 100%;
  }
  &:hover{
    color: #000;
  }
}

.accordion{
  margin-bottom: 100px;
}